import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams } from 'ionic-angular';
import { ViewController } from 'ionic-angular';
import { ScreenOrientation } from '@ionic-native/screen-orientation';

/**
 * Generated class for the DetallecompaniasPage page.
 *
 * See https://ionicframework.com/docs/components/#navigation for more info on
 * Ionic pages and navigation.
 */

@IonicPage()
@Component({
  selector: 'page-detallecompanias',
  templateUrl: 'detallecompanias.html',
})
export class DetallecompaniasPage {
  public backgroundImage = 'assets/img/fondo.jpg'; 
	public compania = 'assets/gif/aseguradoraancon.png'; 
	public imagetexto = 'assets/img/ancon.png';    
 

  public items:  any;
  public error: string;
  public images: any = [];



  constructor(public navCtrl: NavController, public navParams: NavParams,public viewCtrl: ViewController,public screenOrientation: ScreenOrientation) {
     this.items = navParams.data;
     //screenOrientation.lock(screenOrientation.ORIENTATIONS.PORTRAIT);
  }

}



